<template>
  <div class="tags-container">
    <b-container>
      <h1 class="my-4"></h1>
      
      <b-card class="mb-4">
        <template #header>
          <div class="d-flex justify-content-between align-items-center">
            <h4 class="mb-0"></h4>
            <b-button variant="primary" size="sm" @click="showAddTagModal" :disabled="!hasActiveAccount">
              <i class="bi bi-plus-circle me-1"></i> 
            </b-button>
          </div>
          <b-alert v-if="!hasActiveAccount" show variant="warning" class="mt-2 py-2">
            <small> <router-link to="/settings"></router-link> Telegram</small>
          </b-alert>
        </template>
        
        <b-table
          :items="allTags"
          :fields="fields"
          striped
          hover
          responsive
          :busy="isLoading"
          class="mb-0"
        >
          <template #cell(color)="data">
            <div v-if="data.item.color" class="color-preview" :style="{ backgroundColor: data.item.color }"></div>
            <span v-else class="text-muted"></span>
          </template>
          
          <template #cell(count)="data">
            {{ data.item.count || 0 }} 
          </template>
          
          <template #cell(actions)="data">
            <b-button-group size="sm">
              <b-button variant="outline-primary" @click="showEditTagModal(data.item)">
                <i class="bi bi-pencil"></i>
              </b-button>
              <b-button variant="outline-danger" @click="showDeleteTagModal(data.item)">
                <i class="bi bi-trash"></i>
              </b-button>
            </b-button-group>
          </template>
          
          <template #table-busy>
            <div class="text-center my-2">
              <b-spinner class="align-middle"></b-spinner>
              <strong> ...</strong>
            </div>
          </template>
        </b-table>
        
        <div v-if="!isLoading && allTags.length === 0 && hasActiveAccount" class="text-center py-3">
          <p class="text-muted">""</p>
        </div>
      </b-card>
    </b-container>
    
    <b-modal
      v-model="showTagModal"
      :title="isEditing ? '' : ''"
      @ok="handleTagSubmit"
      @hidden="resetTagForm"
      :ok-disabled="!tagForm.name.trim()"
    >
      <b-form>
        <b-form-group label=":" label-for="tag-name">
          <b-form-input
            id="tag-name"
            v-model="tagForm.name"
            required
            placeholder=""
          ></b-form-input>
        </b-form-group>
        
        <b-form-group label=" ():" label-for="tag-color">
          <b-form-input
            id="tag-color"
            v-model="tagForm.color"
            type="color"
          ></b-form-input>
        </b-form-group>
      </b-form>
    </b-modal>
    
    <b-modal
      v-model="showDeleteModal"
      title=""
      ok-variant="danger"
      ok-title=""
      @ok="deleteTag"
    >
      <p> "<strong>{{ tagToDelete ? tagToDelete.name : '' }}</strong>" </p>
      <p class="text-danger"></p>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'Tags',
  data() {
    return {
      // B-table 
      fields: [
        { key: 'name', label: '', sortable: true },
        { key: 'color', label: '' },
        { key: 'count', label: '', sortable: true },
        { key: 'actions', label: '' }
      ],
      showTagModal: false, // /
      showDeleteModal: false, // 
      isEditing: false, // 
      tagForm: { // 
        _id: null, // ID
        name: '',
        color: '#3498db' // 
      },
      tagToDelete: null // 
    };
  },
  computed: {
    //  tags  getters
    ...mapGetters('tags', [
      'allTags',
      'isLoading', // 
      'hasError',  // 
      'errorMessage' // 
    ]),
    //  auth  getters
    ...mapGetters('auth', ['hasActiveAccount']), // 
  },
  created() {
    this.fetchTags(); // 
  },
  methods: {
    //  tags  actions
    ...mapActions('tags', [
      'fetchTags',
      'createTag',
      'updateTag',
      'deleteTag'
    ]),
    
    // 
    showAddTagModal() {
      this.isEditing = false;
      this.resetTagForm(); // 
      this.showTagModal = true;
    },
    
    // 
    showEditTagModal(tag) {
      this.isEditing = true;
      //  Vuex 
      this.tagForm = { ...tag };
      this.showTagModal = true;
    },
    
    // 
    showDeleteTagModal(tag) {
      this.tagToDelete = tag;
      this.showDeleteModal = true;
    },
    
    // /
    async handleTagSubmit(bvModalEvent) {
      bvModalEvent.preventDefault(); // 
      
      if (!this.tagForm.name.trim()) {
        this.$bvToast.toast('', { title: '', variant: 'warning', solid: true });
        return;
      }

      try {
        if (this.isEditing) {
          //  action
          await this.updateTag({
            id: this.tagForm._id, // ID
            tagData: {            // 
              name: this.tagForm.name.trim(),
              color: this.tagForm.color
            }
          });
          this.$bvToast.toast('', {
            title: '',
            variant: 'success',
            solid: true
          });
        } else {
          //  action
          await this.createTag({
            name: this.tagForm.name.trim(),
            color: this.tagForm.color
          });
          this.$bvToast.toast('', {
            title: '',
            variant: 'success',
            solid: true
          });
        }
        this.showTagModal = false; // 
      } catch (error) {
        //  Vuex  errorMessage
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },
    
    // 
    async deleteTag() {
      if (!this.tagToDelete) return;
      
      try {
        //  action
        await this.deleteTag(this.tagToDelete._id);
        this.$bvToast.toast('', {
          title: '',
          variant: 'success',
          solid: true
        });
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      } finally {
        this.tagToDelete = null; // 
      }
    },
    
    // 
    resetTagForm() {
      this.tagForm = {
        _id: null,
        name: '',
        color: '#3498db'
      };
    }
  }
};
</script>

