// src/store/modules/tags.js
import apiClient from '../../utils/api'; //  Axios 

// 
const state = {
  tags: [], // 
  loading: false, // 
  error: null // 
};

// Getters
const getters = {
  allTags: state => state.tags,
  isLoading: state => state.loading,
  hasError: state => !!state.error,
  errorMessage: state => state.error
};

// Mutations
const mutations = {
  SET_TAGS(state, tags) {
    state.tags = tags;
  },
  SET_LOADING(state, loading) {
    state.loading = loading;
  },
  SET_ERROR(state, error) {
    state.error = error;
  },
  ADD_TAG(state, tag) {
    // 
    const existingIndex = state.tags.findIndex(t => t._id === tag._id);
    if (existingIndex !== -1) {
      //  ()
      state.tags.splice(existingIndex, 1, tag);
    } else {
      state.tags.push(tag);
      //  count state.tags.sort((a, b) => b.count - a.count);
    }
  },
  UPDATE_TAG(state, updatedTag) {
    const index = state.tags.findIndex(t => t._id === updatedTag._id);
    if (index !== -1) {
      state.tags.splice(index, 1, updatedTag);
    }
  },
  REMOVE_TAG(state, id) {
    state.tags = state.tags.filter(t => t._id !== id);
  }
};

// Actions
const actions = {
  async fetchTags({ commit }) {
    commit('SET_LOADING', true);
    commit('SET_ERROR', null);

    try {
      //  /api/tags  count 
      const response = await apiClient.get('/tags');
      commit('SET_TAGS', response.data);
      commit('SET_LOADING', false);
    } catch (error) {
      let errorMessage = '';
      if (error.response && error.response.data && error.response.data.message) {
        errorMessage = error.response.data.message;
      }
      commit('SET_ERROR', errorMessage);
      commit('SET_LOADING', false);
      throw error;
    }
  },

  async createTag({ commit, dispatch }, tagData) { // tagData  name  color
    commit('SET_LOADING', true);
    commit('SET_ERROR', null);

    try {
      const response = await apiClient.post('/tags', tagData);
      //  createTag 
      // 
      commit('ADD_TAG', response.data); // ADD_TAG mutation 
      commit('SET_LOADING', false);
      return response.data;
    } catch (error) {
      let errorMessage = '';
      // 
      if (error.response && error.response.status === 400 && error.response.data && error.response.data.message === '') { // 
        errorMessage = '';
      } else if (error.response && error.response.data && error.response.data.message) {
        errorMessage = error.response.data.message;
      }
      commit('SET_ERROR', errorMessage);
      commit('SET_LOADING', false);
      throw error;
    }
  },

  async updateTag({ commit }, { id, tagData }) { // tagData  name  color
    commit('SET_LOADING', true);
    commit('SET_ERROR', null);

    try {
      const response = await apiClient.put(`/tags/${id}`, tagData);
      commit('UPDATE_TAG', response.data);
      commit('SET_LOADING', false);
      return response.data;
    } catch (error) {
      let errorMessage = '';
      // 
      if (error.response && error.response.status === 400 && error.response.data && error.response.data.message === '') { // 
        errorMessage = '';
      } else if (error.response && error.response.data && error.response.data.message) {
        errorMessage = error.response.data.message;
      }
      commit('SET_ERROR', errorMessage);
      commit('SET_LOADING', false);
      throw error;
    }
  },

  async deleteTag({ commit }, id) {
    commit('SET_LOADING', true);
    commit('SET_ERROR', null);

    try {
      await apiClient.delete(`/tags/${id}`);
      commit('REMOVE_TAG', id);
      commit('SET_LOADING', false);
    } catch (error) {
      let errorMessage = '';
      if (error.response && error.response.data && error.response.data.message) {
        errorMessage = error.response.data.message;
      }
      commit('SET_ERROR', errorMessage);
      commit('SET_LOADING', false);
      throw error;
    }
  }
};

export default {
  namespaced: true, // 
  state,
  getters,
  actions,
  mutations
};