// src/router/index.js
import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '../store'; //  Vuex store

// 
import Home from '../views/Home.vue';
import Login from '../views/Login.vue';
import Favorites from '../views/Favorites.vue';
import FavoriteDetail from '../views/FavoriteDetail.vue';
import Tags from '../views/Tags.vue';
import Search from '../views/Search.vue';
import Settings from '../views/Settings.vue';
import NotFound from '../views/NotFound.vue';

Vue.use(VueRouter);

// 
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { guestOnly: true } // 
  },
  {
    path: '/favorites',
    name: 'Favorites',
    component: Favorites,
    meta: { requiresAuth: true } // 
  },
  {
    path: '/favorites/:id',
    name: 'FavoriteDetail',
    component: FavoriteDetail,
    meta: { requiresAuth: true }
  },
  {
    path: '/tags',
    name: 'Tags',
    component: Tags,
    meta: { requiresAuth: true }
  },
  {
    path: '/search',
    name: 'Search',
    component: Search,
    meta: { requiresAuth: true }
  },
  {
    path: '/settings',
    name: 'Settings',
    component: Settings,
    meta: { requiresAuth: true }
  },
  {
    path: '*', // 
    name: 'NotFound',
    component: NotFound
  }
];

const router = new VueRouter({
  mode: 'history', //  HTML5 History  Nginx
  base: process.env.BASE_URL, //  URL Vue CLI 
  routes
});

//  - 
router.beforeEach(async (to, from, next) => {
  //  Vuex store  ( localStorage  token)
  //  Vuex  auth  initializeAuth action
  if (!store.getters['auth/isAuthenticated'] && localStorage.getItem('token')) {
    try {
      await store.dispatch('auth/initializeAuth');
    } catch (error) {
      console.error(':', error);
      //  token  token 
      store.dispatch('auth/logout');
      return next({ path: '/login', query: { redirect: to.fullPath } });
    }
  }

  const isAuthenticated = store.getters['auth/isAuthenticated'];

  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 
    if (!isAuthenticated) {
      //  redirect 
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      });
    } else {
      // 
      next();
    }
  } else if (to.matched.some(record => record.meta.guestOnly)) {
    //  ()
    if (isAuthenticated) {
      //  ( redirect )
      const redirectPath = to.query.redirect || '/favorites';
      next({ path: redirectPath, replace: true });
    } else {
      // 
      next();
    }
  } else {
    // 
    next();
  }
});

export default router;