<template>
  <div class="login-page">
    <b-container>
      <b-row class="justify-content-center">
        <b-col md="6">
          <b-card no-body>
            <b-card-header header-tag="nav">
              <b-nav card-header tabs>
                <b-nav-item :active="activeTab === 'login'" @click="activeTab = 'login'"></b-nav-item>
                <b-nav-item :active="activeTab === 'register'" @click="activeTab = 'register'"></b-nav-item>
              </b-nav>
            </b-card-header>

            <b-card-body>
              <b-alert v-if="hasError" show variant="danger">{{ errorMessage }}</b-alert>
              <b-alert v-if="successMessage" show variant="success">{{ successMessage }}</b-alert>
              
              <div v-if="activeTab === 'login'">
                <h4 class="text-center mb-4">Telegram</h4>
                <b-form @submit.prevent="handleLogin">
                  <b-form-group
                    label=":"
                    label-for="username"
                  >
                    <b-form-input
                      id="username"
                      type="text"
                      v-model="loginForm.username"
                      placeholder=""
                      required
                    ></b-form-input>
                  </b-form-group>
                  
                  <b-form-group
                    label=":"
                    label-for="password"
                  >
                    <b-form-input
                      id="password"
                      type="password"
                      v-model="loginForm.password"
                      placeholder=""
                      required
                    ></b-form-input>
                  </b-form-group>
                  
                  <b-button type="submit" variant="primary" class="w-100" :disabled="isLoading">
                    <b-spinner v-if="isLoading" small type="grow"></b-spinner> 
                  </b-button>
                </b-form>
              </div>
              
              <div v-if="activeTab === 'register'">
                <h4 class="text-center mb-4"></h4>
                <b-form @submit.prevent="handleRegister">
                  <b-form-group
                    label=":"
                    label-for="reg-username"
                  >
                    <b-form-input
                      id="reg-username"
                      type="text"
                      v-model="registerForm.username"
                      placeholder=""
                      required
                    ></b-form-input>
                  </b-form-group>
                  
                  <b-form-group
                    label=":"
                    label-for="reg-password"
                  >
                    <b-form-input
                      id="reg-password"
                      type="password"
                      v-model="registerForm.password"
                      placeholder=""
                      required
                    ></b-form-input>
                  </b-form-group>
                  
                  <b-form-group
                    label=":"
                    label-for="reg-confirm-password"
                  >
                    <b-form-input
                      id="reg-confirm-password"
                      type="password"
                      v-model="registerForm.confirmPassword"
                      placeholder=""
                      required
                      :state="passwordMatchState"
                    ></b-form-input>
                    <b-form-invalid-feedback :state="passwordMatchState">
                        
                    </b-form-invalid-feedback>
                  </b-form-group>

                  <h5 class="mt-4">Telegram</h5>
                  <p class="text-muted small">Telegram</p>
                  
                  <b-form-group label="API ID:" label-for="api-id">
                    <b-form-input id="api-id" type="text" v-model="registerForm.apiId" placeholder="Telegram API ID"></b-form-input>
                  </b-form-group>
                  
                  <b-form-group label="API Hash:" label-for="api-hash">
                    <b-form-input id="api-hash" type="text" v-model="registerForm.apiHash" placeholder="Telegram API Hash"></b-form-input>
                  </b-form-group>
                  
                  <b-form-group label="SESSION_STRING:" label-for="session-string">
                    <b-form-textarea id="session-string" v-model="registerForm.sessionString" rows="3" placeholder="Telegram SESSION_STRING"></b-form-textarea>
                    <small class="form-text text-muted">
                      <a href="#" @click.prevent="showSessionHelp = true">SESSION_STRING?</a>
                    </small>
                  </b-form-group>
                  
                  <b-button type="submit" variant="primary" class="w-100" :disabled="isLoading || !isRegisterFormValid">
                    <b-spinner v-if="isLoading" small type="grow"></b-spinner> 
                  </b-button>
                </b-form>
              </div>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
    
    <b-modal id="session-help-modal" title="SESSION_STRING" v-model="showSessionHelp" ok-only>
      <div>
        <p>Telegram SESSION_STRING</p>
        <ol>
          <li>Python 3.8+</li>
          <li>Telethon: <code>pip install telethon</code></li>
          <li>Python</li>
        </ol>
        <pre><code>from telethon.sync import TelegramClient
from telethon.sessions import StringSession

API_ID = "API_ID"
API_HASH = "API_HASH"

with TelegramClient(StringSession(), API_ID, API_HASH) as client:
    print("Telegram...")
    client.start()
    session_string = client.session.save()
    print(f"SESSION_STRING: {session_string}")</code></pre>
        <p>TelegramSESSION_STRING</p>
        <p class="text-danger">SESSION_STRING</p>
      </div>
    </b-modal>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Login',
  
  data() {
    return {
      activeTab: 'login', // 
      showSessionHelp: false, //  SESSION_STRING 
      successMessage: '', // 
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        confirmPassword: '',
        apiId: '',
        apiHash: '',
        sessionString: ''
      }
    };
  },
  
  computed: {
    ...mapGetters('auth', ['isLoading', 'hasError', 'errorMessage']), //  auth 
    
    passwordMatchState() {
      // 
      if (this.registerForm.password.length === 0 && this.registerForm.confirmPassword.length === 0) {
        return null; // 
      }
      return this.registerForm.password === this.registerForm.confirmPassword;
    },

    isRegisterFormValid() {
      // 
      if (!this.registerForm.username || !this.registerForm.password || !this.registerForm.confirmPassword) {
        return false;
      }
      // 
      if (this.registerForm.password !== this.registerForm.confirmPassword) {
        return false;
      }
      //  Telegram API 
      const { apiId, apiHash, sessionString } = this.registerForm;
      if (apiId || apiHash || sessionString) {
        return apiId && apiHash && sessionString;
      }
      return true;
    }
  },
  
  methods: {
    ...mapActions('auth', ['login', 'register', 'clearError']), //  auth  actions
    
    // 
    async handleLogin() {
      this.clearError(); // 
      this.successMessage = ''; // 

      if (!this.loginForm.username || !this.loginForm.password) {
        this.$bvToast.toast('', { title: '', variant: 'warning', solid: true });
        return;
      }
      
      try {
        const result = await this.login({
          username: this.loginForm.username,
          password: this.loginForm.password
        });
        
        //  redirect 
        const redirectPath = this.$route.query.redirect || '/favorites';
        this.$router.replace(redirectPath); //  replace 
        
        this.loginForm.username = ''; // 
        this.loginForm.password = '';
      } catch (error) {
        //  Vuex  errorMessage
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },
    
    // 
    async handleRegister() {
      this.clearError(); // 
      this.successMessage = ''; // 

      if (!this.isRegisterFormValid) {
        this.$bvToast.toast('Telegram', { title: '', variant: 'warning', solid: true });
        return;
      }
      
      try {
        const userData = {
          username: this.registerForm.username,
          password: this.registerForm.password
        };
        
        // API
        if (this.registerForm.apiId && this.registerForm.apiHash && this.registerForm.sessionString) {
          userData.telegramAccounts = [{
            name: '', // 
            apiId: this.registerForm.apiId,
            apiHash: this.registerForm.apiHash,
            sessionString: this.registerForm.sessionString
          }];
        }
        
        await this.register(userData);
        
        // 
        this.activeTab = 'login';
        this.successMessage = '';
        
        // 
        this.registerForm = {
          username: '',
          password: '',
          confirmPassword: '',
          apiId: '',
          apiHash: '',
          sessionString: ''
        };

        this.$bvToast.toast('', {
          title: '',
          variant: 'success',
          solid: true
        });

      } catch (error) {
        //  Vuex  errorMessage
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    }
  },
  
  created() {
    this.clearError(); // 
  }
};
</script>

