<template>
  <div class="settings-page">
    <h2 class="mb-4"></h2>
    
    <b-alert v-if="hasError" show variant="danger">{{ errorMessage }}</b-alert>
    
    <b-alert v-if="saveSuccess" show variant="success"></b-alert>
    
    <b-card class="mb-4">
      <template #header><h5></h5></template>
      <b-card-body>
        <b-form @submit.prevent="saveSettings">
          <b-form-group label=":" label-for="default-view">
            <b-form-radio-group
              id="default-view"
              v-model="settings.defaultView"
              :options="[{ text: '', value: 'grid' }, { text: '', value: 'list' }]"
              stacked
            ></b-form-radio-group>
          </b-form-group>
          
          <b-form-group label=":" label-for="items-per-page">
            <b-form-select
              id="items-per-page"
              v-model="settings.itemsPerPage"
              :options="[10, 20, 50, 100]"
            ></b-form-select>
          </b-form-group>
          
          <div class="d-flex justify-content-end">
            <b-button type="submit" variant="primary" :disabled="isLoading">
              <b-spinner v-if="isLoading" small type="grow"></b-spinner> 
            </b-button>
          </div>
        </b-form>
      </b-card-body>
    </b-card>
    
    <b-card class="mt-4 mb-4">
      <template #header>
        <div class="d-flex justify-content-between align-items-center">
          <h5 class="mb-0">Telegram</h5>
          <b-button size="sm" variant="success" @click="showAddAccountModal">
            <i class="bi bi-plus-circle me-1"></i> 
          </b-button>
        </div>
      </template>
      <b-card-body>
        <b-alert v-if="telegramAccounts.length === 0" show variant="info">
          <i class="bi bi-info-circle me-1"></i> Telegram
        </b-alert>
        <b-list-group v-else>
          <b-list-group-item v-for="account in telegramAccounts" :key="account.id"
            :class="{ 'border-primary': account.isActive, 'account-item': true }"
            class="d-flex justify-content-between align-items-center"
          >
            <div>
              <h6>{{ account.name || ` ${account.id.substring(0, 8)}...` }}
                <b-badge v-if="account.isActive" variant="primary" class="ms-2"></b-badge>
              </h6>
              <p class="text-muted mb-0 small">
                @{{ account.username || 'N/A' }} (ID: {{ account.telegramId || 'N/A' }})<br/>
                : {{ account.lastSync | $_formatDate({year: 'numeric', month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit'}) }}
              </p>
            </div>
            <div class="d-flex gap-2">
              <b-button v-if="!account.isActive" size="sm" variant="outline-primary" @click="switchActiveAccount(account.id)" :disabled="isLoading">
                
              </b-button>
              <b-button size="sm" variant="outline-danger" @click="confirmDeleteAccount(account)">
                <i class="bi bi-trash"></i> 
              </b-button>
            </div>
          </b-list-group-item>
        </b-list-group>
      </b-card-body>
    </b-card>

    <b-card class="mt-4 mb-4">
      <template #header><h5></h5></template>
      <b-card-body>
        <p v-if="sessionStatus === 'active'" class="text-success mb-2">
          <i class="bi bi-check-circle me-2"></i> Telegram
        </p>
        <p v-else-if="sessionStatus === 'error'" class="text-danger mb-2">
          <i class="bi bi-exclamation-triangle me-2"></i> : {{ sessionError }}
        </p>
        <p v-else class="text-warning mb-2">
          <i class="bi bi-info-circle me-2"></i> Telegram""Telegram
        </p>

        <b-button variant="outline-primary" @click="syncFavorites" :disabled="syncLoading || !hasActiveAccount">
          <b-spinner v-if="syncLoading" small type="grow"></b-spinner> 
        </b-button>
        <small class="text-muted ms-3" v-if="lastSyncTime">
          : {{ lastSyncTime | $_formatDate({year: 'numeric', month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit'}) }}
        </small>
      </b-card-body>
    </b-card>

    <b-card class="mt-4 mb-4" v-if="activeAccount">
      <template #header><h5></h5></template>
      <b-card-body>
        <b-row align-v="center">
          <b-col md="2" cols="4">
            <b-img :src="activeAccount.profilePhoto || defaultAvatar" rounded="circle" fluid alt=""></b-img>
          </b-col>
          <b-col md="10" cols="8">
            <h5>{{ activeAccount.firstName || 'N/A' }} {{ activeAccount.lastName || 'N/A' }}</h5>
            <p class="text-muted">@{{ activeAccount.username || 'N/A' }}</p>
            <p class="mb-0"><strong>Telegram ID:</strong> {{ activeAccount.telegramId || 'N/A' }}</p>
            <p class="mb-0"><strong>:</strong> {{ user.createdAt | $_formatDate() }}</p>
            <p class="mb-0"><strong>:</strong> {{ user.lastLogin | $_formatDate() }}</p>
          </b-col>
        </b-row>
      </b-card-body>
    </b-card>
    <b-card class="mt-4 mb-4" v-else> <template #header><h5></h5></template>
        <b-card-body>
            <p class="text-muted">Telegram</p>
        </b-card-body>
    </b-card>
    
    <b-card class="mt-4 mb-4">
      <template #header><h5></h5></template>
      <b-card-body>
        <b-button variant="danger" @click="confirmLogout">
          <i class="bi bi-box-arrow-right me-1"></i> 
        </b-button>
      </b-card-body>
    </b-card>

    <b-modal
      id="add-account-modal"
      :title="isEditingAccount ? 'Telegram' : 'Telegram'"
      v-model="showAddAccountModalForm"
      @ok="handleAddAccountSubmit"
      @hidden="resetAddAccountForm"
      :ok-disabled="!isAddAccountFormValid"
      :ok-title="isEditingAccount ? '' : ''"
    >
      <b-form>
        <b-form-group label=" ():" label-for="modal-account-name">
          <b-form-input id="modal-account-name" v-model="addAccountForm.name" placeholder=""></b-form-input>
        </b-form-group>
        <b-form-group label="API ID:" label-for="modal-api-id">
          <b-form-input id="modal-api-id" v-model="addAccountForm.apiId" required></b-form-input>
          <div class="form-text"> <a href="https://my.telegram.org/apps" target="_blank">my.telegram.org/apps</a> </div>
        </b-form-group>
        <b-form-group label="API Hash:" label-for="modal-api-hash">
          <b-form-input id="modal-api-hash" v-model="addAccountForm.apiHash" required></b-form-input>
        </b-form-group>
        <b-form-group label="SESSION_STRING:" label-for="modal-session-string">
          <b-form-textarea id="modal-session-string" v-model="addAccountForm.sessionString" rows="3" required></b-form-textarea>
          <div class="form-text">
            Telegram
            <a href="#" @click.prevent="showSessionHelp = true">SESSION_STRING?</a>
          </div>
        </b-form-group>
      </b-form>
    </b-modal>

    <b-modal id="session-help-modal" title="SESSION_STRING" v-model="showSessionHelp" ok-only>
      <div>
        <p>Telegram SESSION_STRING</p>
        <ol>
          <li>Python 3.8+</li>
          <li>Telethon: <code>pip install telethon</code></li>
          <li>Python</li>
        </ol>
        <pre><code>from telethon.sync import TelegramClient
from telethon.sessions import StringSession

API_ID = "API_ID"
API_HASH = "API_HASH"

with TelegramClient(StringSession(), API_ID, API_HASH) as client:
    print("Telegram...")
    client.start()
    session_string = client.session.save()
    print(f"SESSION_STRING: {session_string}")</code></pre>
        <p>TelegramSESSION_STRING</p>
        <p class="text-danger">SESSION_STRING</p>
      </div>
    </b-modal>
    
    <b-modal id="delete-account-modal" title="" ok-variant="danger" ok-title="" @ok="deleteAccount">
      <p> "{{ accountToDelete ? accountToDelete.name : '' }}" </p>
      <p class="text-danger"></p>
    </b-modal>

    <b-modal id="logout-modal" title="" ok-variant="danger" ok-title="" @ok="logout">
      <p></p>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'Settings',
  
  data() {
    return {
      saveSuccess: false, // 
      showSessionHelp: false, // SESSION_STRING 
      showAddAccountModalForm: false, // /
      isEditingAccount: false, // 
      accountToDelete: null, // 
      
      // 
      settings: {
        defaultView: 'grid',
        itemsPerPage: 20
      },
      
      // /
      addAccountForm: {
        id: null, // 
        name: '',
        apiId: '',
        apiHash: '',
        sessionString: ''
      },

      defaultAvatar: require('@/assets/img/default-avatar.png'), // 
    };
  },
  
  computed: {
    ...mapGetters('auth', [
      'currentUser', //  currentUser  user
      'isLoading', // 
      'hasError',  // 
      'errorMessage', // 
      'telegramAccounts', //  Telegram 
      'activeAccount', // 
      'hasActiveAccount', // 
      'syncLoading', // 
      'lastSyncTime' // 
    ]),

    // user getter  currentUser
    user: {
      get() { return this.currentUser; },
      set() {} // 
    },
    
    // 
    isAddAccountFormValid() {
      const { apiId, apiHash, sessionString } = this.addAccountForm;
      return apiId && apiHash && sessionString;
    }
  },
  
  methods: {
    ...mapActions('auth', [
      'updateUserSettings', // 
      'logout',             // 
      'addTelegramAccount', //  Telegram 
      'switchTelegramAccount', // 
      'updateTelegramAccount', //  Telegram 
      'deleteTelegramAccount', //  Telegram 
      'syncFavorites'        // 
    ]),
    
    // 
    async saveSettings() {
      try {
        await this.updateUserSettings(this.settings);
        this.saveSuccess = true;
        
        setTimeout(() => {
          this.saveSuccess = false;
        }, 3000); // 3
      } catch (error) {
        console.error(':', error);
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },
    
    // 
    showAddAccountModal() {
      this.isEditingAccount = false;
      this.resetAddAccountForm();
      this.showAddAccountModalForm = true;
    },

    // 
    showEditAccountModal(account) {
      this.isEditingAccount = true;
      //  Vuex 
      this.addAccountForm = { 
        id: account.id,
        name: account.name,
        // API 
        // 
        apiId: '', 
        apiHash: '', 
        sessionString: '' 
      };
      this.showAddAccountModalForm = true;
    },

    // /
    async handleAddAccountSubmit(bvModalEvent) {
      bvModalEvent.preventDefault(); // 
      
      if (!this.isAddAccountFormValid) {
        this.$bvToast.toast('API', { title: '', variant: 'warning', solid: true });
        return;
      }
      
      try {
        const accountData = {
          name: this.addAccountForm.name.trim() || undefined, //  name 
          apiId: this.addAccountForm.apiId.trim(),
          apiHash: this.addAccountForm.apiHash.trim(),
          sessionString: this.addAccountForm.sessionString.trim()
        };

        if (this.isEditingAccount) {
          await this.updateTelegramAccount({
            accountId: this.addAccountForm.id,
            updateData: accountData
          });
          this.$bvToast.toast('', { title: '', variant: 'success', solid: true });
        } else {
          await this.addTelegramAccount(accountData);
          this.$bvToast.toast('Telegram', { title: '', variant: 'success', solid: true });
        }
        
        this.showAddAccountModalForm = false; // 
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },

    // 
    resetAddAccountForm() {
      this.addAccountForm = {
        id: null,
        name: '',
        apiId: '',
        apiHash: '',
        sessionString: ''
      };
      this.isEditingAccount = false;
    },
    
    // 
    async switchActiveAccount(accountId) {
      try {
        await this.switchTelegramAccount(accountId);
        this.$bvToast.toast('', { title: '', variant: 'success', solid: true });
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },
    
    // 
    confirmDeleteAccount(account) {
      this.accountToDelete = account;
      this.$bvModal.show('delete-account-modal');
    },

    // 
    async deleteAccount() {
      if (!this.accountToDelete) return;
      
      try {
        await this.deleteTelegramAccount(this.accountToDelete.id);
        this.$bvToast.toast('', { title: '', variant: 'success', solid: true });
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      } finally {
        this.accountToDelete = null;
      }
    },
    
    // 
    confirmLogout() {
      this.$bvModal.show('logout-modal');
    },

    // 
    formatDate(dateString) {
      return this.$_formatDate(dateString); // 
    }
  },
  
  // 
  async created() {
    // 
    if (this.isAuthenticated) { // 
      try {
        await this.fetchCurrentUser(); //  action  activeAccount  telegramAccounts
        //  settings 
        if (this.user && this.user.settings) {
          this.settings = { ...this.user.settings };
        }
      } catch (error) {
        // fetchCurrentUser  token 
        console.error(":", error);
      }
    }
  }
};
</script>

