<template>
  <div class="favorite-detail-page">
    <b-container>
      <h1 class="my-4"></h1>

      <b-alert v-if="hasError" show variant="danger">{{ errorMessage }}</b-alert>
      <div v-else-if="isLoading" class="text-center my-5">
        <b-spinner class="text-primary"></b-spinner>
        <p class="mt-2">...</p>
      </div>
      <b-alert v-else-if="!favorite" show variant="warning">
        <i class="bi bi-exclamation-triangle me-1"></i> 
      </b-alert>

      <b-card v-else class="mb-4">
        <b-card-body>
          <b-row>
            <b-col md="8">
              <div class="main-content-display mb-3">
                <template v-if="favorite.type === 'photo'">
                  <b-img :src="favorite.content.url" fluid alt=""></b-img>
                </template>
                <template v-else-if="favorite.type === 'video'">
                  <video :src="favorite.localPath" controls fluid class="w-100" v-if="favorite.localPath">
                    
                  </video>
                  <b-img v-else :src="favorite.content.thumbnailUrl || videoPlaceholder" fluid alt=""></b-img>
                  <div v-if="!favorite.localPath" class="text-muted text-center mt-2">
                    <i class="bi bi-info-circle me-1"></i> 
                  </div>
                </template>
                <template v-else-if="favorite.type === 'audio'">
                  <audio :src="favorite.localPath" controls class="w-100" v-if="favorite.localPath">
                    
                  </audio>
                  <div v-else class="text-muted text-center mt-2">
                     <i class="bi bi-info-circle me-1"></i> 
                  </div>
                </template>
                 <template v-else-if="favorite.type === 'document'">
                    <a :href="favorite.localPath || '#'" target="_blank" class="btn btn-outline-primary w-100 py-3">
                        <i class="bi bi-file-earmark-arrow-down-fill me-2"></i> : {{ favorite.content.fileName }}
                    </a>
                     <div v-if="!favorite.localPath" class="text-muted text-center mt-2">
                       <i class="bi bi-info-circle me-1"></i> 
                    </div>
                </template>
                <template v-else-if="favorite.type === 'link'">
                  <a :href="favorite.content.url" target="_blank" class="btn btn-outline-info w-100 py-3">
                    <i class="bi bi-link-45deg me-2"></i> : {{ favorite.content.url | $_truncateText(50) }}
                  </a>
                </template>
                <template v-else-if="favorite.type === 'text'">
                  <div class="card p-3 bg-light text-start">
                    <h5 class="mb-2">:</h5>
                    <pre class="mb-0">{{ favorite.content.text }}</pre>
                  </div>
                </template>
                <template v-else>
                  <div class="text-center p-5 bg-light">
                    <i class="bi bi-question-circle display-4 text-muted"></i>
                    <p class="mt-2"></p>
                  </div>
                </template>
              </div>

              <h4 class="mb-2">{{ favorite.content.caption || favorite.content.text || '' }}</h4>
              <p class="text-muted small">
                : {{ favorite.savedAt | $_formatDate() }} <br/>
                : {{ favorite.category || '' }} <br/>
                ID: {{ favorite.telegramMessageId }}
              </p>

              <div class="d-flex gap-2 mt-3">
                <b-button variant="outline-danger" @click="confirmDelete">
                  <i class="bi bi-trash me-1"></i> 
                </b-button>
              </div>
            </b-col>

            <b-col md="4">
              <b-card header="" class="mb-3">
                <div class="tags-list">
                  <b-badge
                    v-for="tag in favorite.tags"
                    :key="tag._id || tag"
                    pill
                    class="tag-badge-style me-1 mb-1"
                    @click="removeTag(tag)"
                  >
                    {{ tag.name || tag }} <i class="bi bi-x-circle-fill ms-1"></i>
                  </b-badge>
                </div>
                <hr />
                <b-form @submit.prevent="addTag">
                  <b-form-group label=":" label-for="add-tag-input">
                    <b-form-input
                      id="add-tag-input"
                      v-model="newTag"
                      placeholder=""
                      required
                    ></b-form-input>
                  </b-form-group>
                  <b-button type="submit" variant="outline-primary" size="sm" class="mt-2" :disabled="!newTag.trim()"></b-button>
                </b-form>
              </b-card>

              <b-card header="" class="mb-3">
                <p><strong>ID:</strong> {{ favorite.content.fileId || 'N/A' }}</p>
                <p><strong>:</strong> {{ favorite.content.fileName || 'N/A' }}</p>
                <p><strong>:</strong> {{ favorite.content.fileSize ? (favorite.content.fileSize / 1024 / 1024).toFixed(2) + ' MB' : 'N/A' }}</p>
                <p><strong>MIME:</strong> {{ favorite.content.mimeType || 'N/A' }}</p>
                <p><strong>:</strong> {{ favorite.content.duration ? favorite.content.duration + ' ' : 'N/A' }}</p>
                <p><strong>:</strong> {{ favorite.content.width || 'N/A' }} x {{ favorite.content.height || 'N/A' }}</p>
                <p><strong>:</strong> 
                    <i v-if="favorite.isDownloaded" class="bi bi-check-circle-fill text-success"></i>
                    <i v-else class="bi bi-x-circle-fill text-danger"></i>
                    {{ favorite.isDownloaded ? '' : '' }}
                </p>
                <p v-if="favorite.localPath"><strong>:</strong> {{ favorite.localPath }}</p>
              </b-card>
            </b-col>
          </b-row>
        </b-card-body>
      </b-card>
    </b-container>
    
    <b-modal id="delete-modal-detail" title="" ok-variant="danger" ok-title="" @ok="deleteFavorite">
      <p></p>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'FavoriteDetail',
  data() {
    return {
      newTag: '', // 
      // 
      imagePlaceholder: require('@/assets/img/image-placeholder.jpg'),
      videoPlaceholder: require('@/assets/img/video-placeholder.jpg'),
    };
  },
  computed: {
    ...mapGetters('favorites', [
      'currentFavorite', 
      'isLoading', 
      'hasError', 
      'errorMessage'
    ]),
    favorite() {
      return this.currentFavorite;
    }
  },
  methods: {
    ...mapActions('favorites', [
      'fetchFavoriteById', 
      'deleteFavorite', 
      'updateFavoriteTags',
      'removeTagFromFavorite'
    ]),
    
    // 
    async addTag() {
      if (!this.newTag.trim()) {
        this.$bvToast.toast('', { title: '', variant: 'warning', solid: true });
        return;
      }
      // 
      const existingTag = this.favorite.tags.find(
        t => (typeof t === 'string' ? t.toLowerCase() : t.name.toLowerCase()) === this.newTag.trim().toLowerCase()
      );
      if (existingTag) {
        this.$bvToast.toast('', { title: '', variant: 'info', solid: true });
        return;
      }

      try {
        // 
        //  favorite.tags tags
        const updatedTags = [...this.favorite.tags.map(t => typeof t === 'string' ? t : t.name), this.newTag.trim()];
        
        //  Vuex action 
        await this.updateFavoriteTags({
          favoriteId: this.favorite._id,
          tags: updatedTags // 
        });
        
        this.newTag = ''; // 
        this.$bvToast.toast('', { title: '', variant: 'success', solid: true });
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },
    
    // 
    async removeTag(tagToRemove) {
      if (!confirm(` "${typeof tagToRemove === 'string' ? tagToRemove : tagToRemove.name}" `)) {
        return;
      }

      try {
        const tagName = typeof tagToRemove === 'string' ? tagToRemove : tagToRemove.name;
        //  Vuex action 
        await this.removeTagFromFavorite({
          favoriteId: this.favorite._id,
          tagName: tagName // 
        });
        this.$bvToast.toast('', { title: '', variant: 'success', solid: true });
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    },

    // 
    confirmDelete() {
      this.$bvModal.show('delete-modal-detail');
    },
    
    // 
    async deleteFavorite() {
      try {
        await this.deleteFavorite(this.favorite._id);
        this.$bvToast.toast('', {
          title: '',
          variant: 'success',
          solid: true
        });
        // 
        this.$router.push('/favorites');
      } catch (error) {
        this.$bvToast.toast(this.errorMessage || '', {
          title: '',
          variant: 'danger',
          solid: true
        });
      }
    }
  },
  
  // ID
  created() {
    const favoriteId = this.$route.params.id;
    if (favoriteId) {
      this.fetchFavoriteById(favoriteId);
    }
  },
  // 
  watch: {
    '$route.params.id': {
      handler(newId) {
        if (newId) {
          this.fetchFavoriteById(newId);
        }
      },
      immediate: true // 
    }
  }
};
</script>

