<template>
  <div id="app">
    <header>
      <b-navbar toggleable="lg" type="dark" variant="info">
        <b-container>
          <b-navbar-brand to="/">Telegram</b-navbar-brand>
          <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
          <b-collapse id="nav-collapse" is-nav>
            <b-navbar-nav>
              <b-nav-item to="/" exact></b-nav-item>
              <b-nav-item to="/favorites"></b-nav-item>
              <b-nav-item to="/tags"></b-nav-item>
            </b-navbar-nav>

            <b-navbar-nav class="ml-auto">
              <b-nav-form class="d-flex my-2 my-lg-0">
                <b-form-input
                  size="sm"
                  class="mr-sm-2"
                  placeholder="..."
                  v-model="searchQuery"
                  @keyup.enter="onSearch"
                ></b-form-input>
                <b-button size="sm" class="my-2 my-sm-0" type="submit" @click.prevent="onSearch" variant="outline-light">
                  <i class="bi bi-search"></i>
                </b-button>
              </b-nav-form>

              <b-nav-item-dropdown right v-if="isLoggedIn">
                <template #button-content>
                  <em class="text-white">{{ username }}</em>
                </template>
                <b-dropdown-item to="/settings"></b-dropdown-item>
                <b-dropdown-item @click="confirmLogout"></b-dropdown-item>
              </b-nav-item-dropdown>
              <b-nav-item v-else to="/login"></b-nav-item>
            </b-navbar-nav>
          </b-collapse>
        </b-container>
      </b-navbar>
    </header>

    <main class="container mt-4 flex-grow-1">
      <router-view />
    </main>

    <footer class="footer mt-5 py-3 bg-light text-center">
      <b-container>
        <span class="text-muted">Telegram &copy; {{ new Date().getFullYear() }}</span>
      </b-container>
    </footer>

    <b-modal id="logout-modal" title="" ok-variant="danger" ok-title="" @ok="logout">
      <p></p>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'App',
  data() {
    return {
      searchQuery: ''
    };
  },
  computed: {
    ...mapGetters('auth', ['isLoggedIn', 'username'])
  },
  methods: {
    ...mapActions('auth', ['logout']), //  auth  logout action
    
    // 
    onSearch() {
      if (this.searchQuery.trim()) {
        // 
        this.$router.push({
          path: '/search',
          query: { q: this.searchQuery.trim() }
        }).catch(() => {}); // 
        this.searchQuery = ''; // 
      }
    },

    // 
    confirmLogout() {
      this.$bvModal.show('logout-modal');
    }
  },
  //  Vuex 
  created() {
    //  isLoggedIn  localStorage  token 
    if (!this.isLoggedIn && localStorage.getItem('token')) {
      this.$store.dispatch('auth/initializeAuth');
    }
  }
};
</script>

